
















































































































































import HelloWorld from './components/HelloWorld.vue'
import { Vue, Component } from 'vue-property-decorator'
import * as PIXI from 'pixi.js'
import img from './assets/bonny.jpg'
import sub = PIXI.groupD8.sub

@Component({
  components: {
    HelloWorld
  },
  mounted() {
    let app: PIXI.Application = new PIXI.Application({
      width: 700,
      height: 450
    })

    app.loader.add(img).load(setup)

    function setup() {
      // console.log('set up')

      let sprite: PIXI.Sprite = new PIXI.Sprite(
        app.loader.resources[img].texture
      )

      app.stage.addChild(sprite)

      sprite.x = 0
      sprite.y = 0
    }

    let element: null | HTMLElement = document.getElementById('canvas')
    if (element) {
      element.appendChild(app.view)
    }
  }
})
export default class App extends Vue {
  drawerRight: any = null
  right: boolean = false
  tmp: any = 2
  activeStep: number = 1
  subStep: number = 1

  back(): void {
    if (this.subStep > 1) {
      this.subStep--
    } else if (this.activeStep === 2) {
      this.activeStep = 1
      this.subStep = 4
    }
  }

  next(): void {
    if (this.subStep < 3) {
      this.subStep++
    } else if (this.activeStep === 1 && this.subStep === 4) {
      this.activeStep = 2
      this.subStep = 1
    } else if (this.activeStep === 1) {
      this.subStep++
    }
  }
}
